module.exports = (sequelize, DataTypes) => {
  const DrugTherapy = sequelize.define("drug_therapy", {
    Id: {
      type: DataTypes.UUID,
      defaultValue: DataTypes.UUIDV4,
      allowNull: false,
      primaryKey: true,
      field: "id"
    },
    name: { type: DataTypes.STRING }
  });

  return DrugTherapy;
};
